import React from 'react'
import './Nav.css';
import { Container } from 'react-bootstrap';
import { Nav } from 'react-bootstrap';
import { Navbar } from 'react-bootstrap';
import { NavDropdown } from 'react-bootstrap';
import { Row } from 'react-bootstrap';
import { Col } from 'react-bootstrap';
import {Link} from 'react-router-dom';


import logo from '../Images/Logo.jpg';

export default function Navv() {
    return (
        <>
            <Container fluid>
                <Container className="navcontainer">
                    <Row className="navrow">
                        <Col md={12} className="text-center">
                            <Navbar expand="lg">
                                <Link className="navbar-brand" to="/">
                                    <img
                                        src={logo}
                                        className="nav-logo d-inline-block align-top"
                                        alt="Johnnette Technologies logo"/></Link>
                                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                                <Navbar.Collapse id="basic-navbar-nav">
                                    <Nav className="mr-auto">
                                        <Link className="nav-link" to="/About">About Us</Link>
                                        <Link className="nav-link" to="/Products">Products</Link>
                                        <Link className="nav-link" to="/Industries">Industries</Link>
                                        <Link className="nav-link" to="/Blog">Blog</Link>
                                        <li className="nav-item dropdown">
                                            <a className="nav-link" href="# " id="navbarDropdownMenuLink" role="button"
                                                data-bs-toggle="dropdown" aria-expanded="false">Education</a>
                                            <ul className="dropdown-menu cust-dropdown" aria-labelledby="navbarDropdownMenuLink">
                                                <NavDropdown.Item href="#action3">Drone Pilot Training</NavDropdown.Item>
                                                <NavDropdown.Item href="#action3">Drone Courses</NavDropdown.Item>
                                            </ul>
                                        </li>
                                        {/* <NavDropdown title="Education" id="basic-nav-dropdown" role="menu">
                                            <NavDropdown.Item href="#action/3.1">Drone Pilot Training</NavDropdown.Item>
                                            <NavDropdown.Item href="#action/3.2">Drone Courses</NavDropdown.Item>
                                        </NavDropdown> */}
                                        <Nav.Link href="#link">Contact</Nav.Link>
                                    </Nav>
                                </Navbar.Collapse>
                            </Navbar>
                        </Col>
                    </Row>
                </Container>
            </Container>
        </>
    )
}

