import React from "react";
import './Home.css';
import { Card } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css"
import "swiper/components/navigation/navigation.min.css"



// Industries
import coverimage from '../Images/Cover Photo.jpg';
import surveyimage from '../Images/Home/Survey.jpg';
import securityimage from '../Images/Home/Security.jpg';
import cinemaimage from '../Images/Home/Cinema.jpg';
import militaryimage from '../Images/Home/Military.jpg';
import industrialimage from '../Images/Home/Industrial.jpg';
import agricultureimage from '../Images/Home/Agriculture.jpg';

// Product Images
import jf2image from '../Images/Products/JF2.png';
import jr5image from '../Images/Products/JR5.png';
import npntimage from '../Images/Products/NPNT.png';

// Client Logos
import apimage from '../Images/Client-Logo/ap-logo.jpg';
import armyimage from '../Images/Client-Logo/army-logo.jpg';
import assamimage from '../Images/Client-Logo/assam-rifles-logo.jpg';
import csirimage from '../Images/Client-Logo/csir-logo.jpg';
import dellimage from '../Images/Client-Logo/dell-logo.jpg';
import dgcaimage from '../Images/Client-Logo/dgca-logo.jpg';
import drdoimage from '../Images/Client-Logo/drdo-logo.jpg';
import geosysimage from '../Images/Client-Logo/geosys-logo.jpg';
import greenkoimage from '../Images/Client-Logo/greenko-logo.jpg';
import relianceimage from '../Images/Client-Logo/reliance-logo.jpg';



import SwiperCore, {
    Autoplay, Pagination, Navigation
} from 'swiper/core';

SwiperCore.use([Autoplay, Pagination, Navigation]);


export default function Home() {
    return (
        <div className="container-fluid p-0">
            <div className="">
                {/* Cover */}
                <div className="row">
                    <div className="col-sm-12">
                        <div className="img-div">
                            <img
                                src={coverimage}
                                className="cover-image"
                                alt="Johnnette Technologies logo" />
                            <h1 className="home-jumbo">
                                Making Impossible Possible
                            </h1>
                        </div>
                    </div>
                </div>
            </div>

            {/* Why Johnnette */}
            <div className="container py-3">
                <div className="row company-intro">
                    <div className="col-sm-12 text-center">
                        <h2 className="text-center py-4">Why Johnnette?</h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-3"></div>
                    <div className="col-sm-6">
                        <p className="">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Recusandae omnis illum, saepe
                            voluptas
                            accusantium eaque non distinctio. Odio accusantium, facere sed laborum hic, quis, temporibus magni
                            ad culpa reprehenderit soluta!</p>
                    </div>
                    <div className="col-sm-3"></div>
                </div>
            </div>

            {/* Industry */}
            <div className="container py-3">
                <div className="row">
                    <div className="col-sm-12">
                        <h2 className="text-center discov py-3">Discover Johnnette Products for Your Industry</h2>
                    </div>
                </div>
                <div className="row text-center indus-row">
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Survey">
                            <img
                                src={surveyimage}
                                className="indus-image"
                                alt="Survey Application of Drones" />
                            <p className="img-caption">Survey</p>
                        </Link>
                    </div>
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Security">
                            <img
                                src={securityimage}
                                className="indus-image"
                                alt="Security Application of Drones" />
                            <p className="img-caption">Security</p>
                        </Link>
                    </div>
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Cinema">
                            <img
                                src={cinemaimage}
                                className="indus-image"
                                alt="Cinema Application of Drones" />
                            <p className="img-caption">Cinema</p>
                        </Link>
                    </div>
                </div>
                <div className="row text-center indus-row">
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Military">
                            <img
                                src={militaryimage}
                                className="indus-image"
                                alt="Military Application of Drones" />
                            <p className="img-caption">Military</p>
                        </Link>
                    </div>
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Industrial">
                            <img
                                src={industrialimage}
                                className="indus-image"
                                alt="Industrial Application of Drones" />
                            <p className="img-caption">Industrial</p>
                        </Link>
                    </div>
                    <div className="col-sm-4">
                        <Link className="indus-link" to="/Agriculture">
                            <img
                                src={agricultureimage}
                                className="indus-image"
                                alt="Agriculture Application of Drones" />
                            <p className="img-caption">Agriculture</p>
                        </Link>
                    </div>
                </div>
            </div>

            {/* Our Products */}
            <div className="container py-3">
                <div className="row">
                    <div className="col-sm-12">
                        <h2 className="text-center prod py-4">Our Products</h2>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-12">
                        <Swiper slidesPerView={2} spaceBetween={0} loop={true} centeredSlides={false} autoplay={{
                            "delay": 10000,
                            "disableOnInteraction": false
                        }} navigation={true} className="mySwiper">
                            <SwiperSlide>
                                <Card>
                                    <Card.Body>
                                        <Card.Title>JF2</Card.Title>
                                        <div className="image-wrap">
                                            <Card.Img variant="top" src={jf2image} />
                                            <div className="desc-layer">
                                                <p className="desc-text">Desciption on Image Hover</p>
                                            </div>
                                        </div>
                                        <Card.Text>
                                            <span className="headng">Category <br /></span>
                                            <span>Micro</span>
                                            <br />
                                            <span className="headng">Endurance <br /></span>
                                            <span>45 mins</span>
                                            <br />
                                            <span className="headng">Range <br /></span>
                                            <span>15 Km</span>
                                        </Card.Text>
                                        <Link to="/JF2"><Button className="cust-btn">Know more about JF2</Button></Link>
                                    </Card.Body>
                                </Card>
                            </SwiperSlide>
                            <SwiperSlide>
                                <Card>
                                    <Card.Body>
                                        <Card.Title>JR5</Card.Title>
                                        <div className="image-wrap">
                                            <Card.Img variant="top" src={jr5image} />
                                            <div className="desc-layer">
                                                <p className="desc-text">Desciption on Image Hover</p>
                                            </div>
                                        </div>
                                        <Card.Text>
                                            <span className="headng">Category <br /></span>
                                            <span>Micro</span>
                                            <br />
                                            <span className="headng">Endurance <br /></span>
                                            <span>45 mins</span>
                                            <br />
                                            <span className="headng">Range <br /></span>
                                            <span>15 Km</span>
                                        </Card.Text>
                                        <Link to="/JF2"><Button className="cust-btn">Know more about JR5</Button></Link>
                                    </Card.Body>
                                </Card>
                            </SwiperSlide>
                            <SwiperSlide>
                                <Card>
                                    <Card.Body>
                                        <Card.Title>NPNT Solution</Card.Title>
                                        <div className="image-wrap">
                                            <Card.Img variant="top" src={npntimage} />
                                            <div className="desc-layer">
                                                <p className="desc-text">Desciption on Image Hover</p>
                                            </div>
                                        </div>
                                        <Card.Text>
                                        <span className="headng">Category <br /></span>
                                            <span>Micro</span>
                                            <br />
                                            <span className="headng">Endurance <br /></span>
                                            <span>45 mins</span>
                                            <br />
                                            <span className="headng">Range <br /></span>
                                            <span>15 Km</span>
                                        </Card.Text>
                                        <Link to="/JF2"><Button className="cust-btn">Know more about NPNT</Button></Link>
                                    </Card.Body>
                                </Card>
                            </SwiperSlide>
                            <SwiperSlide>
                                <Card>
                                    <Card.Body>
                                        <Card.Title>Jobot-2A</Card.Title>
                                        <div className="image-wrap">
                                            <Card.Img variant="top" src={jf2image} />
                                            <div className="desc-layer">
                                                <p className="desc-text">Desciption on Image Hover</p>
                                            </div>
                                        </div>
                                        <Card.Text>
                                        <span className="headng">Category <br /></span>
                                            <span>Micro</span>
                                            <br />
                                            <span className="headng">Endurance <br /></span>
                                            <span>45 mins</span>
                                            <br />
                                            <span className="headng">Range <br /></span>
                                            <span>15 Km</span>
                                        </Card.Text>
                                        <Link to="/JF2"><Button className="cust-btn">Know more about Jobot</Button></Link>
                                    </Card.Body>
                                </Card>
                            </SwiperSlide>
                        </Swiper>
                    </div>
                </div>
            </div >

            {/* Client Logos */}
            <div className="container py-3">
                <div className="row">
                    <div className="col-sm-12">
                        <h2 className="text-center clien py-4">Our Clients</h2>
                    </div>
                </div>

                <div className="row">
                    <div className="col-sm-12">
                        <Swiper slidesPerView={4} spaceBetween={50} centeredSlides={false} autoplay={{
                            "delay": 2000,
                            "disableOnInteraction": false
                        }} navigation={false} className="mySwiper">
                            <SwiperSlide><img
                                src={apimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={armyimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={assamimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={csirimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={dellimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={dgcaimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={drdoimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={geosysimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={greenkoimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                            <SwiperSlide><img
                                src={relianceimage}
                                className="client-image"
                                alt="Survey Application of Drones" /></SwiperSlide>
                        </Swiper>
                    </div>
                </div>
            </div >
        </div >
    )
}

